:root {
  --font-family: "Gilroy", sans-serif;
  --content-width: 1492px;
  --content-haeder-width: 1790px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --light-color: #fff;
  --main-color: #FBD784;
}

